import { createStore } from "vuex";

// 创建一个新的 Vuex Store
const store = createStore({
  state() {
    return {
      isLoading: false,
      clickTimes: 0,
    };
  },
  mutations: {
    // 将 state 作为参数
    reverseLoadState(state) {
      // state.isLoading true/false 互转
      state.isLoading = !state.isLoading;
    },
    addClickTimes(state, payload) {
        if (payload === undefined) {
            payload = 1;
        }
      state.clickTimes += payload;
    },
  },
});

export default store;
